import React from "react";
import Layout from "../../layouts/default";
import PageHeader from "../../components/PageHeader";
import PageContent from "../../components/PageContent";
import QuestionCard from "../../components/QuestionCard";
import Link from "../../components/Link";
import questions from "./questions.yaml";

const toggleQuestionCard = (question) => {
  if (question.childNodes[1].style.display === "none") {
    question.childNodes[1].style = "display: block;";
  }
  else {
    question.childNodes[1].style = "display: none;";
  }
};

export default (props) => (
  <Layout
    title="Frequently Asked Questions"
    description="Go through the frequently asked questions to find answer to your questions. If your question is not answered here, join Bastion HQ: https://discord.gg/fzx8fkt"
    image="https://resources.bastionbot.org/og/a5dc9c3d3634c0561ce0e5d65280bf86.jpg"
  >
    <PageHeader title="Frequently Asked Questions">
      <p>
        Have a question that is not listed here? No worries mate, just head
        over to the <Link>#bastion-support</Link> channel in <Link
        to="https://discord.gg/fzx8fkt"> Bastion HQ</Link>, and ask it.
      </p>
    </PageHeader>

    <PageContent>
      {
        questions.map((testimonial, i) => (
          <QuestionCard
            question={ testimonial.question }
            answer={ testimonial.answer }
            handleClick={ toggleQuestionCard }
            key={ i }
          />
        ))
      }
    </PageContent>
  </Layout>
);
